import React, { FunctionComponent, useMemo } from 'react'
import styled from '@emotion/styled'
import PostItem from './PostItem'
import CategoryList from 'components/Main/CategoryList'
import { PostListItemType } from '../../types/PostItem.types'
import { ScrollDetector } from 'components/Common/CommonUI'

const PostListWrapper = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 25px;
  max-width: 991px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  padding-top: 70px;
  margin-top: 10px;
  margin-bottom: 100px;

  @media (max-width: 768px) {
    display: block;
    margin-bottom: 50px;
  }
`
const SelectedCategoryWrap = styled.div`
  width: 100%;
`
const SelectedListTitle = styled.h1`
  width: 100%;
  max-width: 768px;
  //text-align: center;
  font-size: 20px;
  color: #e84545;
  margin: 0 auto;
  text-decoration: underline;
  padding: 8px 15px;
`
// const PostItemBox = styled(PostItem)`
//   background: red;
//   &:first-of-type {
//     grid-column-start: 1;
//     grid-column-end: 3;
//   }
// `

const ContentContainer = styled.div`
  position: relation;
  -webkit-transition: all 0.45s ease-in-out;
  transition: all 0.45s ease-in-out;
  &.addMT {
    margin-top: 80px;
    -webkit-transition: all 0.65s ease-in-out;
    transition: all 0.65s ease-in-out;
  }
`

export type PostType = {
  node: {
    id: string
    frontmatter: {
      title: string
      summary: string
      date: string
      categories: string[]
      thumbnail: {
        publicURL: string
      }
    }
  }
}
type PostListProps = {
  categoryList: {
    [key: string]: number
  }
  selectedCategory: string
  posts: PostListItemType[]
}

const PostList: FunctionComponent<PostListProps> = function ({
  selectedCategory,
  posts,
  categoryList,
}) {
  const postListData = useMemo(
    () =>
      posts.filter(
        ({
          node: {
            frontmatter: { categories },
          },
        }: PostListItemType) =>
          selectedCategory !== 'All'
            ? categories.includes(selectedCategory)
            : true,
      ),
    [selectedCategory],
  )
  const isScroll = ScrollDetector()
  return (
    <ContentContainer className={isScroll ? 'addMT' : ''}>
      <CategoryList
        categoryList={categoryList}
        selectedCategory={selectedCategory}
      />
      {/* <SelectedCategoryWrap>
        <SelectedListTitle>#{selectedCategory}</SelectedListTitle>
      </SelectedCategoryWrap> */}
      <PostListWrapper>
        {postListData.map(
          ({
            node: {
              id,
              fields: { slug },
              frontmatter,
            },
          }: PostListItemType) => (
            <PostItem {...frontmatter} link={slug} key={id} />
          ),
        )}
      </PostListWrapper>
    </ContentContainer>
  )
}

export default PostList
