const CategoryWrap = styled.div`
  display: flex;
  flex-wrap: wrap;
`
const CategoryItem = styled(Link)`
  color: #ffffff;
  border: 1px solid #e84545;
  background: #e84545;
  opacity: 0.7;
  display: inline-block;
  line-height: 1em;
  margin-right: 4px;
  margin-bottom: 4px;
  font-size: 11px;
  font-weight: 500;
  padding: 3px 8px 5px 8px;
  //border-radius: 2px;
  transition: all 0.2s ease-in;
  &:hover {
    color: #fff;
    background: #e84545;
  }
`
const PostSummary = styled.p`
  width: 100%;
  font-size: 14px;
  line-height: 1.5em;
  margin-bottom: 15px;
  color: #222222;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
`
const PostDate = styled.span`
  font-size: 12px;
  color: #666666;
  line-height: 1em;
  margin-bottom: 12px;
`

const PostItemWrapper = styled(Link)`
  display: flex;
  flex-direction: column;
  // border-radius: 10px;
  // box-shadow: 0 0 8px rgba(0, 0, 0, 0.15);
  // transition: 0.3s box-shadow;
  cursor: pointer;
  border-top: 0;
  position: relative;
  padding: 25px 20px;
  background: #ffffff;
  &:hover {
    //box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    ${PostTitle} > h2 {
      background-size: 100% 4px;
    }
  }
  &:nth-of-type(2n-1) {
    border-left: 0;
  }
  &:first-of-type {
    grid-column-start: 1;
    grid-column-end: 3;

    & > ${ThumbnailImage} {
      height: 300px;
    }
  }
  @media (max-width: 768px) {
    margin-bottom: 25px;
    & > ${ThumbnailImage} {
      height: 40vw !important;
    }
  }
`

const PostItem: FunctionComponent<PostItemProps> = function ({
  title,
  date,
  categories,
  summary,
  thumbnail: {
    childImageSharp: { gatsbyImageData },
  },
  link,
}) {
  return (
    <PostItemWrapper to={link}>
      <ThumbnailImage image={gatsbyImageData} alt="Post Item Image" />
      <PostItemContent>
        <PostTitle>
          <h2>{title}</h2>
        </PostTitle>
        <PostDate>{date}</PostDate>
        <PostSummary>{summary}</PostSummary>
        <CategoryWrap>
          {categories.map(category => (
            <CategoryItem to={`/?category=${category}`} key={category}>
              {category}
            </CategoryItem>
          ))}
        </CategoryWrap>
      </PostItemContent>
    </PostItemWrapper>
  )
}
export default PostItem
