import React, { FunctionComponent, ReactNode, useState, useRef } from 'react'
import styled from '@emotion/styled'
import { Link } from 'gatsby'
import { ScrollDetector } from 'components/Common/CommonUI'
import { Swiper, SwiperSlide } from 'swiper/react'
import { FreeMode } from 'swiper'
import 'swiper/css'
import 'swiper/css/free-mode'

type CategoryItemProps = {
  active: boolean
}

type GatsbyLinkProps = {
  children: ReactNode
  className?: string
  to: string
} & CategoryItemProps

export type CategoryListProps = {
  selectedCategory: string
  categoryList: {
    [key: string]: number
  }
}

const CategoryListWrapper = styled.div`
  width: 100%;
  height: 55px;
  background: #ffffff;
  position: fixed;
  top: 240px;
  -webkit-transition: all 0.65s ease-in-out;
  transition: all 0.65s ease-in-out;
  padding-left: 15px;
  border-bottom: 1px solid #efefef;
  &.fixed {
    position: fixed;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.08);
    top: 60px;
    border-bottom: 0;
    -webkit-transition: all 0.65s ease-in-out;
    transition: all 0.65s ease-in-out;
    z-index: 997;
  }
`

const CategoryItem = styled(({ active, ...props }: GatsbyLinkProps) => (
  <Link {...props} />
))<CategoryItemProps>`
  margin-right: 20px;
  padding: 8px 15px;
  font-size: 12px;
  font-weight: ${({ active }) => (active ? '800' : '400')};
  cursor: pointer;
  background: #e84545;
  // color: #53354a;
  background: #999999;
  opacity: ${({ active }) => (active ? '0.9' : '0.6')};
  color: #ffffff;
  border-radius: 25px;
  transition: 0.2s;
  &:last-of-type {
    margin-right: 0;
  }

  &:hover {
    color: #53354a;
  }
`
const List = styled(Swiper)`
  width: 100%;
  max-width: 991px;
  margin: 0 auto;
  margin-top: 7px;
  height: 45px;
  align-item: center;
  .swiper-slide {
    width: auto;
    padding-top: 3px;
  }
`

const CategoryList: FunctionComponent<CategoryListProps> = function ({
  selectedCategory,
  categoryList,
}) {
  const isScroll = ScrollDetector()
  return (
    <CategoryListWrapper className={isScroll ? 'fixed' : ''}>
      <List
        slidesPerView={'auto'}
        spaceBetween={8}
        modules={[FreeMode]}
        className="mySwiper"
      >
        {Object.entries(categoryList).map(([name, count]) => (
          <SwiperSlide>
            <CategoryItem
              to={`/?category=${name}`}
              active={name === selectedCategory}
              key={name}
            >
              #{name}({count})
            </CategoryItem>
          </SwiperSlide>
        ))}
      </List>
    </CategoryListWrapper>
  )
}

export default CategoryList
